.forSpace {
  margin-top: 100px;
}
#Header1 {
  position: relative;
  text-align: center;
}
#Paragraph1 {
  margin-top: 35px;
  margin-left: 50px;
}

.userIntro {
  /* User Introduction */
  padding: 1rem;
  display: flex;
  flex-direction: column;
  row-gap: 25px;
  box-sizing: border-box;
  margin-left: 150px;
}
#userDemo_Image3 {
  width: 400px;
  position: absolute;
  margin-left: 550px;
  margin-top: 75px;
}

.user_Hobbies {
  /* User Hobbies div */
  box-sizing: border-box;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  margin-left: 200px;
}
.Hobbies {
  /*  ul */
  list-style: none;
  padding: 1px;
}
#Paragraph2 {
  /* Quote Author */
  margin-left: 200px;
  font-size: large;
}

#Header2 {
  /* Professional Header */
  margin-top: 30px;
}

.professionalSkills {
  /* Skills Div */
  box-sizing: border-box;
  margin-top: 30px;
  padding: 0.5rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 0px;
  row-gap: 40px;
  justify-content: center;
  margin-left: 200px;
}
.skill {
  box-sizing: border-box;
  max-width: 120px;
  padding: 10px;
  border-radius: 15px;
  align-self: center;
  border: 1px solid white;
}
.skill:hover {
  /* Each skill */
  cursor: pointer;
  opacity: 1;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  animation: zoom-in-zoom-out 1s;
  background: -webkit-linear-gradient(
    to right,
    #f64f59,
    #c471ed,
    #12c2e9,
    white
  );
  background: linear-gradient(to right, #f64f59, #c471ed, #12c2e9, white);
  border: none;
}

@keyframes zoom-in-zoom-out {
  /* At the beginning of the animation */
  0% {
    /* Scale the element to its original size */
    transform: scale(1, 1);
    background-color: rgba(6, 7, 6, 0.479);
  }
  /* At the middle of the animation */
  50% {
    /* Scale the element to 1.5 times its original size */
    transform: scale(1.5, 1.5);
    background-color: rgba(12, 12, 12, 0.473);
  }
  /* At the end of the animation */
  100% {
    /* Scale the element back to its original size */
    transform: scale(1, 1);
    background-color: rgba(8, 8, 8, 0.521);
  }
}

.Tools {
  /* Tools Div */
  box-sizing: border-box;
  margin-top: 30px;
  padding: 0.5rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 0px;
  row-gap: 40px;
  justify-content: center;
  margin-left: 200px;
}

